import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { PostIt } from '../postIt';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-post-it',
  templateUrl: './post-it.component.html',
  styleUrls: ['./post-it.component.css']
})
export class PostItComponent implements OnInit {

  selectedUrl: string;
  titel: string;

  constructor(public dialogRef: MatDialogRef<PostItComponent>,
              @Inject(MAT_DIALOG_DATA) public postIt: PostIt, public sanitizer: DomSanitizer) { }

  ngOnInit() {
    this.titel = this.postIt.titel;
    this.titel = this.titel.replace('-', '');
    this.selectedUrl = this.postIt.urls[0];
  }

  getUrl() {
    return this.sanitizer.bypassSecurityTrustResourceUrl(this.selectedUrl);
  }

  selectUrl(url) {
    this.selectedUrl = url;
  }

}
